// Use $c-color to prevent interfering with already declared SCSS variables in the theme
$c-dark-main: #053740;
$c-dark-2: #1e4046;
$c-dark-3: #062037;

$c-gray-main: #627281;
$c-gray-light: #c4cdd4;
$c-gray-light-2: #f9fafc;
$c-white: #FFFFFF;

$c-blue-light: #30ABBB;
$c-blue-light-2: darken($c-blue-light, 10%);

$c-green: #74c78a;
$c-placeholder: #C4CDD5;
$c-table-hover: #f1f3f6;
$c-table-border: rgba(98, 114, 129, 0.15);
$c-danger: #dc3545;

// Font-weight
$fw-semi-bold: 600;
$fw-normal: normal;
$font-family: 'Open Sans', sans-serif;
$font-family-italic: Adobe Garamond Pro, sans-serif;

// Breakpoints
// https://getbootstrap.com/docs/4.3/layout/overview/#responsive-breakpoints
$br-xs: 576px; // Extra small devices (portrait phones, less than 576px)
$br-sm: 768px; // Small devices (landscape phones, less than 768px)
$br-md: 992px; // Medium devices (tablets, less than 992px)
$br-lg: 1200px; // Large devices (desktops, less than 1200px)
$br-xl: 1920px; // Extra large
$br-tablet: 1024px;

// Offsets
$new-app-version-banner-offset: 70px;
